import React from 'react'
import Helmet from 'react-helmet'

import shareMeta from '../content/shareMeta'
import monthNames from '../content/monthNames'
import postBodyToComponent from '../utils/postBodyToComponent'
import Container from '../components/Container'
import FadeInUp from '../components/FadeInUp'

import showdown from 'showdown'

const mdConverter = new showdown.Converter();

const PostTemplate = ({
  data: {
    contentfulBlog: post
  }
}) => {
  const datetime = new Date(post.published)
  const DD = datetime.getDate()
  const MMMM = monthNames[datetime.getMonth()]
  const YYYY = datetime.getFullYear()
  return (
    <FadeInUp>
      <article>
        <Helmet>
          <title>{post.title}</title>

          {/*<h3 className='f6 ttu tracked near-black mb0'>
            By {post.author.first_name} {post.author.last_name}
          </h3>*/}
          <meta name='description' content={post.summary.summary} />
          <meta property='og:url' content={`http://timshi.ai/blog/${post.slug}`} />

          {/*Twitter*/}
          <meta property='twitter:card' content={post.featured_image ? 'summary_large_image' : 'summary'} />
          <meta property='twitter:site' content={shareMeta.twitter.site} />
          <meta property='twitter:creator' content={shareMeta.twitter.site} />
          <meta property='twitter:description' content={post.summary.summary} />
          <link ref='canonical' href={`http://timshi.ai/blog/${post.slug}`} />

          {/* Facebook open graph meta */}
          <meta property='og:title' content={post.title} />
          <meta property='og:description' content={post.summary.summary} />
          {/*<meta property='og:image' content={post.featured_image || shareMeta.facebook.image} />*/}
          {/* Twitter card meta */}
        </Helmet>
        <header className='sans-serif pv4 pv5-ns'>
          <Container>
            <div className='mw7 center '>
              <time className='f6 mb2 dib ttu tracked'>
                <small>{`${DD} ${MMMM}, ${YYYY}`}</small>
              </time>
              <h1 className='f2 f1-m f-headline-l lh-title mv0'>
                <span className='lh-copy pa1 tracked-tight'>
                  {post.title}
                </span>
              </h1>
              <h2 className='f5 f3-l f4-m f5-ns fw1 georgia i'>
                {post.summary.summary}
              </h2>
            </div>
          </Container>
        </header>
          <section className='pv3 pv4-ns'>
            <Container>
              <div className='georgia mw7 center'>
                {postBodyToComponent(mdConverter.makeHtml(post.article.article))}
              </div>
            </Container>
          </section>
      </article>
    </FadeInUp>
  )
}

export default PostTemplate

// The post template's GraphQL query. Notice the "slug"
// variable which is passed in. We set this on the page
// context in gatsby-node.js.
//
// All GraphQL queries in Gatsby are run at build-time and
// loaded as plain JSON files so have minimal client cost.
// eslint-disable-next-line no-undef
export const pageQuery = graphql `
  query PostBySlug ($slug: String!) {
    # select the post which equals this slug
    contentfulBlog(slug: { eq: $slug }) {
      # specify the fields from the post we need
      slug
      title
      summary {
        id
        summary
      }
      article {
        id
        article
      }
      published
    }
  }
`



